// å¼•å…¥æ ·å¼ï¼ˆç”± webpack å¤„ç†ï¼‰
import './styles/analects.css';

/**
 * AnalectsSDK - å¹²å‡€å¯ç”¨çš„æœ€å°å®ç°
 * æ»¡è¶³ï¼š
 * 1) ç« èŠ‚ç­›é€‰ä¸ºå•é€‰ã€éšè—åœ†åœˆ
 * 2) é»˜è®¤â€œå…¨éƒ¨ç« èŠ‚â€ä¸ºé€‰ä¸­ï¼›é€‰ä¸­å…¶å®ƒç« èŠ‚æ—¶ï¼Œâ€œå…¨éƒ¨ç« èŠ‚â€å˜ä¸ºç™½åº•
 * 3) åˆ‡æ¢ç« èŠ‚/ç­›é€‰æ—¶é‡ç½®åˆ†é¡µï¼Œæœç»é‡å¤æˆ–ä¸²ç« æ•°æ®
 * 4) åˆå§‹ä¸è‡ªåŠ¨æœç´¢ï¼Œåªæœ‰ç‚¹å‡»â€œæœç´¢â€åæ‰è§¦å‘ï¼›æ»šåŠ¨åŠ è½½ä»…åœ¨æœ‰è¿‡æœç´¢åå¯ç”¨
 * 5) çŠ¶æ€æ¡æ˜¾ç¤ºâ€œå…±æ‰¾åˆ°Xæ¡ç»“æœï¼Œæ»šåŠ¨æˆ–ç‚¹å‡»åŠ è½½æ›´å¤šâ€ï¼›å…¨éƒ¨åŠ è½½åæ˜¾ç¤ºâ€œå·²åŠ è½½å®Œæ¯•â€
 *
 * è¯´æ˜ï¼š
 * - é€šè¿‡ Supabase PostgREST è®¿é—®ï¼š/rest/v1
 * - æœåŠ¡ç«¯è¿‡æ»¤ï¼šå…³é”®è¯ï¼ˆor=ilike*ï¼‰ã€ç« èŠ‚ï¼ˆeqï¼‰ï¼Œåˆ†é¡µï¼ˆlimit/offsetï¼‰
 * - å…³è”è¿‡æ»¤ï¼ˆäººç‰©/è®ºç‚¹/è°šè¯­ï¼‰åœ¨å®¢æˆ·ç«¯å®Œæˆ AND è¯­ä¹‰ï¼ˆé€šè¿‡åµŒå¥—é€‰æ‹©è¿”å›çš„ id åˆ—è¡¨åˆ¤æ–­ï¼‰
 * - ç»“æœæ€»æ•°é€šè¿‡ Prefer: count=exact + Content-Range è¯»å–
 */
export default class AnalectsSDK {
  constructor(config = {}) {
    this.supabaseUrl = config.supabaseUrl || '';
    this.supabaseKey = config.supabaseKey || '';
    this.apiBaseUrl = this.supabaseUrl ? `${this.supabaseUrl}/rest/v1` : '';

    this.headers = {
      'apikey': this.supabaseKey,
      'Authorization': `Bearer ${this.supabaseKey}`,
      'Content-Type': 'application/json',
      // å…³é”®ï¼šç”¨äºæ‹¿åˆ° Content-Range ç»Ÿè®¡æ€»æ•°
      'Prefer': 'return=representation,count=exact'
    };

    // æœç´¢/åˆ†é¡µçŠ¶æ€
    this.state = {
      didSearch: false,
      page: 0,
      pageSize: 10,
      hasMore: false,
      totalCount: 0,
      isLoading: false,
      currentFilters: null,
      searchToken: 0
    };

    // é€‰é¡¹ç¼“å­˜
    this.cache = {
      characters: null,
      arguments: null,
      proverbs: null,
      chapters: null,
    };

    // é€‰ä¸­é¡¹
    this.selected = {
      chapter: { id: 'all', name: 'å…¨éƒ¨ç« èŠ‚' },
      characters: new Map(), // id -> name
      arguments: new Map(),
      proverbs: new Map(),
    };
  }

  // ---------- åŸºç¡€è¯·æ±‚ ----------
  async _fetch(url) {
    if (!this.supabaseUrl || !this.supabaseKey) {
      throw new Error('Supabase é…ç½®æ— æ•ˆï¼šè¯·åœ¨åˆå§‹åŒ–æ—¶ä¼ å…¥ supabaseUrl ä¸ supabaseKey');
    }
    const res = await fetch(url, { headers: this.headers });
    if (!res.ok) {
      const text = await res.text().catch(() => '');
      throw new Error(`è¯·æ±‚å¤±è´¥ ${res.status}: ${text || res.statusText}`);
    }
    const contentRange = res.headers.get('content-range');
    const total = contentRange && contentRange.includes('/') ? parseInt(contentRange.split('/').pop(), 10) : null;
    const data = await res.json();
    return { data, total };
  }

  // ---------- é€‰é¡¹è·å–ï¼ˆä¸€æ¬¡æ€§åŠ è½½å¹¶ç¼“å­˜ï¼‰ ----------
  async _getOptions(endpoint, pick = (r) => ({ id: r.id, name: r.name || r.title || r.content })) {
    if (this.cache[endpoint]) return this.cache[endpoint];
    const url = `${this.apiBaseUrl}/${endpoint}?select=id,name,title,content&order=id.asc&limit=1000`;
    const { data } = await this._fetch(url);
    const cleaned = (Array.isArray(data) ? data : []).map(pick).filter(x => x && x.id != null);
    this.cache[endpoint] = cleaned;
    return cleaned;
  }
  getCharacters() { return this._getOptions('characters'); }
  getArguments()  { return this._getOptions('arguments'); }
  getProverbs()   { return this._getOptions('proverbs'); }
  async getChapters() {
    if (this.cache.chapters) return this.cache.chapters;
    // ç›´æ¥ä» analects_entries èšåˆç« èŠ‚
    const url = `${this.apiBaseUrl}/analects_entries?select=chapter&order=chapter.asc&limit=10000`;
    const { data } = await this._fetch(url);
    const set = new Set();
    (data || []).forEach(r => { if (r && r.chapter != null) set.add(String(r.chapter)); });
    const chapters = Array.from(set).map((c, i) => ({ id: c, name: c }));
    this.cache.chapters = chapters;
    return chapters;
  }

  // ---------- æ•°æ®æŸ¥è¯¢ ----------
  _buildEntriesUrl(filters, page, pageSize) {
    const select =
      'id,chapter,section_number,original_text,translation,annotation,' +
      'entry_characters(character_id),' +
      'entry_arguments(argument_id),' +
      'entry_proverbs(proverb_id)';
    let url = `${this.apiBaseUrl}/analects_entries?select=${encodeURIComponent(select)}&order=id.asc`;

    const conds = [];

    // å…³é”®è¯ï¼ˆæœåŠ¡ç«¯ç²—è¿‡æ»¤ï¼‰
    if (filters.keyword) {
      const kw = filters.keyword.trim();
      if (kw) {
        const encoded = encodeURIComponent(`(original_text.ilike.*${kw}*,translation.ilike.*${kw}*,annotation.ilike.*${kw}*)`);
        conds.push(`or=${encoded}`);
      }
    }

    // ç« èŠ‚ï¼ˆæœåŠ¡ç«¯ï¼‰
    if (filters.chapter && filters.chapter !== 'all') {
      conds.push(`chapter=eq.${encodeURIComponent(filters.chapter)}`);
    }

    // åˆ†é¡µ
    conds.push(`limit=${pageSize}`);
    conds.push(`offset=${page * pageSize}`);

    if (conds.length) url += '&' + conds.join('&');
    return url;
  }

  async fetchEntries(filters, page = 0, pageSize = 10) {
    const url = this._buildEntriesUrl(filters, page, pageSize);
    const { data, total } = await this._fetch(url);
    // å®¢æˆ·ç«¯ AND è¿‡æ»¤ï¼ˆäººç‰©/è®ºç‚¹/è°šè¯­ï¼‰
    let list = Array.isArray(data) ? data : [];

    const allIn = (selectedIds, entryIds) =>
      [...selectedIds].every(id => entryIds.includes(parseInt(id, 10)));

    if (filters.characterIds && filters.characterIds.length) {
      list = list.filter(e => {
        const ids = (e.entry_characters || []).map(x => x.character_id).filter(x => x != null);
        return allIn(filters.characterIds, ids);
      });
    }
    if (filters.argumentIds && filters.argumentIds.length) {
      list = list.filter(e => {
        const ids = (e.entry_arguments || []).map(x => x.argument_id).filter(x => x != null);
        return allIn(filters.argumentIds, ids);
      });
    }
    if (filters.proverbIds && filters.proverbIds.length) {
      list = list.filter(e => {
        const ids = (e.entry_proverbs || []).map(x => x.proverb_id).filter(x => x != null);
        return allIn(filters.proverbIds, ids);
      });
    }

    // å…³é”®è¯äºŒæ¬¡è¿‡æ»¤ï¼ˆä¿è¯ä¸æœåŠ¡ç«¯ä¸€è‡´ï¼‰
    if (filters.keyword) {
      const kw = filters.keyword.trim().toLowerCase();
      list = list.filter(e => {
        const t1 = (e.original_text || '').toLowerCase();
        const t2 = (e.translation || '').toLowerCase();
        const t3 = (e.annotation || '').toLowerCase();
        return t1.includes(kw) || t2.includes(kw) || t3.includes(kw);
      });
    }

    return {
      data: list,
      // æ³¨æ„ï¼štotal ä¸ºæœåŠ¡ç«¯ï¼ˆä»…å…³é”®è¯+ç« èŠ‚ï¼‰ç»Ÿè®¡å€¼ï¼Œè¿™é‡Œç”¨å®ƒä½œä¸ºåŸºæ•°ï¼›
      // å®¢æˆ·ç«¯ AND è¿‡æ»¤åæ•°é‡å¯èƒ½æ›´å°‘ï¼Œå› æ­¤æˆ‘ä»¬ä»¥ list.length æ˜¯å¦ç­‰äº pageSize åˆ¤æ–­æ˜¯å¦â€œå¯èƒ½è¿˜æœ‰æ›´å¤šâ€
      totalCount: typeof total === 'number' ? total : list.length,
      hasMore: list.length === pageSize
    };
  }

  // ---------- æ¯æ—¥è®ºè¯­ ----------
  async renderDailyAnalect(container) {
    if (!container) return;
    container.innerHTML = `<div class="analects-card"><div class="analects-loading">åŠ è½½æ¯æ—¥è®ºè¯­...</div></div>`;

    try {
      // è·å–æ€»æ•°
      const countUrl = `${this.apiBaseUrl}/analects_entries?select=id`;
      const { total } = await this._fetch(countUrl);
      const totalCount = typeof total === 'number' ? total : 0;
      if (!totalCount) {
        container.innerHTML = `<div class="analects-error">æš‚æ— æ•°æ®</div>`;
        return;
      }

      // ç”¨æ—¥æœŸï¼ˆUTCï¼‰å†³å®š offset
      const today = new Date();
      const dayIndex = Math.floor((Date.UTC(today.getFullYear(), today.getMonth(), today.getDate()) / 86400000)) % totalCount;

      const url = `${this.apiBaseUrl}/analects_entries?select=id,chapter,section_number,original_text,translation,annotation&order=id.asc&limit=1&offset=${dayIndex}`;
      const { data } = await this._fetch(url);
      const item = (data || [])[0];

      if (!item) {
        container.innerHTML = `<div class="analects-error">æœªæ‰¾åˆ°æ¯æ—¥è®ºè¯­</div>`;
        return;
      }

      container.innerHTML = `
        <div class="analects-card">
          <div class="analects-card-header">
            <h3 class="analects-card-title">ğŸ“œ æ¯æ—¥è®ºè¯­</h3>
            <div class="analects-card-subtitle">${this._fmtChapter(item)}</div>
          </div>
          <div class="analects-card-body">
            <div class="analects-text">${this._safe(item.original_text)}</div>
            ${item.translation ? `<div class="analects-subtext">${this._safe(item.translation)}</div>` : ''}
            ${item.annotation ? `<div class="analects-subtext muted">${this._safe(item.annotation)}</div>` : ''}
          </div>
        </div>
      `;
    } catch (err) {
      container.innerHTML = `<div class="analects-error">åŠ è½½å¤±è´¥ï¼š${err.message}</div>`;
    }
  }

  _fmtChapter(e) {
    const ch = e.chapter != null ? String(e.chapter) : '';
    const sec = e.section_number != null ? String(e.section_number) : '';
    if (ch && sec) return `ç¬¬ ${ch} ç¯‡ Â· ç¬¬ ${sec} ç« `;
    if (ch) return `ç¬¬ ${ch} ç¯‡`;
    return '';
  }

  _safe(t) {
    return (t || '').replace(/[&<>]/g, s => ({'&':'&amp;','<':'&lt;','>':'&gt;'}[s]));
  }

  // ---------- æœç´¢ç•Œé¢ ----------
  async renderSearchInterface(container) {
    if (!container) return;
    container.innerHTML = this._searchSkeleton();

    // ç»‘å®šå…ƒç´ 
    const keywordInput = container.querySelector('#analects-keyword');
    const searchBtn = container.querySelector('#analects-search-btn');
    const results = container.querySelector('#analects-results');
    const statusBar = container.querySelector('#analects-status');
    const loadMoreBtn = container.querySelector('#analects-load-more-btn');
    const chapterCloud = container.querySelector('#chapter-filters');
    const charCloud = container.querySelector('#character-filters');
    const argCloud = container.querySelector('#argument-filters');
    const provCloud = container.querySelector('#proverb-filters');

    // åˆå§‹çŠ¶æ€ï¼šä¸æœç´¢
    statusBar.textContent = 'è¯·è¾“å…¥å…³é”®è¯æˆ–é€‰æ‹©ç­›é€‰åç‚¹å‡»â€œæœç´¢â€ã€‚';

    // æ¸²æŸ“ç­›é€‰é€‰é¡¹
    const [chapters, characters, argumentsList, proverbs] = await Promise.all([
      this.getChapters(), this.getCharacters(), this.getArguments(), this.getProverbs()
    ]);

    // ç« èŠ‚ï¼ˆå•é€‰ + â€œå…¨éƒ¨ç« èŠ‚â€ï¼‰
    this._renderChapterChips(chapterCloud, chapters);

    // å…¶ä½™ä¸ºå¤šé€‰
    this._renderMultiChips(charCloud, characters, this.selected.characters);
    this._renderMultiChips(argCloud, argumentsList, this.selected.arguments);
    this._renderMultiChips(provCloud, proverbs, this.selected.proverbs);

    // ç‚¹å‡»æœç´¢
    searchBtn.addEventListener('click', async () => {
      await this._startSearch({
        keyword: (keywordInput.value || '').trim(),
        characterIds: Array.from(this.selected.characters.keys()),
        argumentIds: Array.from(this.selected.arguments.keys()),
        proverbIds: Array.from(this.selected.proverbs.keys()),
        chapter: this.selected.chapter?.id || 'all'
      }, { results, statusBar, loadMoreBtn });
    });

    // åŠ è½½æ›´å¤šï¼ˆç‚¹å‡»ï¼‰
    loadMoreBtn.addEventListener('click', async () => {
      await this._loadMore({ results, statusBar, loadMoreBtn });
    });

    // æ»šåŠ¨åŠ è½½ï¼ˆä»…åœ¨å·²æœç´¢åç”Ÿæ•ˆï¼‰
    const onScroll = async () => {
      if (!this.state.didSearch || this.state.isLoading || !this.state.hasMore) return;
      const rect = loadMoreBtn.getBoundingClientRect();
      // å½“æŒ‰é’®æ¥è¿‘è§†å£åº•éƒ¨æ—¶è§¦å‘
      if (rect.top < window.innerHeight + 120) {
        await this._loadMore({ results, statusBar, loadMoreBtn });
      }
    };
    window.addEventListener('scroll', onScroll);
  }

  _searchSkeleton() {
    return `
      <div class="analects-card">
        <div class="analects-card-header">
          <h3 class="analects-card-title">ğŸ” è®ºè¯­æŸ¥è¯¢</h3>
          <div class="analects-searchbar">
            <input id="analects-keyword" class="analects-input" placeholder="è¾“å…¥å…³é”®è¯ï¼ˆåŸæ–‡/è¯‘æ–‡/æ³¨é‡Šï¼‰">
            <button id="analects-search-btn" class="analects-btn primary">æœç´¢</button>
          </div>
        </div>
        <div class="analects-card-body">
          <div class="analects-filters">
            <div class="analects-filter">
              <div class="analects-filter-title">ç« èŠ‚</div>
              <div id="chapter-filters" class="analects-tag-cloud"></div>
            </div>
            <div class="analects-filter">
              <div class="analects-filter-title">äººç‰©</div>
              <div id="character-filters" class="analects-tag-cloud"></div>
            </div>
            <div class="analects-filter">
              <div class="analects-filter-title">è®ºç‚¹</div>
              <div id="argument-filters" class="analects-tag-cloud"></div>
            </div>
            <div class="analects-filter">
              <div class="analects-filter-title">è°šè¯­</div>
              <div id="proverb-filters" class="analects-tag-cloud"></div>
            </div>
          </div>
          <div id="analects-status" class="analects-status muted"></div>
          <div id="analects-results" class="analects-results"></div>
          <div class="analects-loadmore">
            <button id="analects-load-more-btn" class="analects-btn">åŠ è½½æ›´å¤š</button>
          </div>
        </div>
      </div>
    `;
  }

  _renderChapterChips(container, chapters) {
    container.innerHTML = '';
    // â€œå…¨éƒ¨ç« èŠ‚â€
    const all = this._chip({ id: 'all', name: 'å…¨éƒ¨ç« èŠ‚' }, true, true);
    container.appendChild(all);

    const setSelected = (chip) => {
      // æ¸…ç©ºæ‰€æœ‰ç« èŠ‚é€‰ä¸­
      container.querySelectorAll('.analects-chip').forEach(el => el.classList.remove('selected'));
      // é€‰ä¸­å½“å‰
      chip.classList.add('selected');
      // æ›´æ–° selected.chapter
      this.selected.chapter = { id: chip.dataset.id, name: chip.dataset.name };
      // è§„åˆ™ï¼šåªæœ‰åœ¨ selected æ—¶ï¼Œ.all-option æ‰æ˜¯ç»¿è‰²ï¼›å¦åˆ™ä¸ºç™½åº•
      const allChip = container.querySelector('.analects-chip.all-option');
      if (allChip) {
        if (chip === allChip) allChip.classList.add('selected');
        else allChip.classList.remove('selected');
      }
      // åˆ‡æ¢ç­›é€‰æ—¶ï¼šé‡ç½®åˆ†é¡µä¸æœç´¢çŠ¶æ€ï¼Œä½†ä¸è§¦å‘è‡ªåŠ¨æœç´¢ï¼ˆç”¨æˆ·éœ€ç‚¹â€œæœç´¢â€ï¼‰
      this._resetPaging();
    };

    all.addEventListener('click', () => setSelected(all));

    (chapters || []).forEach(ch => {
      const chip = this._chip({ id: String(ch.id), name: String(ch.name) }, false, false);
      chip.addEventListener('click', () => setSelected(chip));
      container.appendChild(chip);
    });
  }

  _renderMultiChips(container, options, storeMap) {
    container.innerHTML = '';
    (options || []).forEach(opt => {
      const chip = this._chip({ id: String(opt.id), name: String(opt.name || opt.title || opt.content || opt.id) }, false, false);
      chip.addEventListener('click', () => {
        const id = chip.dataset.id;
        const name = chip.dataset.name;
        if (chip.classList.contains('selected')) {
          chip.classList.remove('selected');
          storeMap.delete(id);
        } else {
          chip.classList.add('selected');
          storeMap.set(id, name);
        }
        this._resetPaging();
      });
      container.appendChild(chip);
    });
  }

  _chip(opt, selected, isAll) {
    const chip = document.createElement('label');
    chip.className = `analects-chip${isAll ? ' all-option' : ''}${selected ? ' selected' : ''}`;
    chip.dataset.id = String(opt.id);
    chip.dataset.name = String(opt.name);
    // éšè—çš„å•é€‰/å¤é€‰è¾“å…¥ï¼ˆä¾›æ— éšœç¢/é”®ç›˜ä½¿ç”¨ï¼›æ ·å¼ä¸Šéšè—å°åœ†åœˆ/æ–¹æ¡†ï¼‰
    chip.innerHTML = `
      <input type="${isAll ? 'radio' : 'checkbox'}" ${selected ? 'checked' : ''} aria-hidden="true" tabindex="-1">
      <span>${this._safe(opt.name)}</span>
    `;
    return chip;
  }

  _resetPaging() {
    this.state.page = 0;
    this.state.hasMore = false;
    this.state.totalCount = 0;
    this.state.didSearch = false; // é˜²æ­¢æœªç‚¹å‡»â€œæœç´¢â€æ—¶è§¦å‘æ»šåŠ¨åŠ è½½
  }

  async _startSearch(filters, ctx) {
    // è®°å½•æœ¬æ¬¡æœç´¢ tokenï¼Œé¿å…æ—§ç»“æœæ’é˜Ÿ
    const token = ++this.state.searchToken;
    this.state.isLoading = true;
    this.state.page = 0;
    this.state.didSearch = true;
    this.state.currentFilters = { ...filters };

    ctx.statusBar.textContent = 'ğŸ” æœç´¢ä¸­...';
    ctx.results.innerHTML = '<div class="analects-loading">æ­£åœ¨æœç´¢...</div>';
    ctx.loadMoreBtn.disabled = true;

    try {
      const { data, totalCount, hasMore } = await this.fetchEntries(this.state.currentFilters, 0, this.state.pageSize);
      if (token !== this.state.searchToken) return; // å·²æœ‰æ–°æœç´¢è§¦å‘

      this.state.page = 1;
      this.state.hasMore = hasMore;
      this.state.totalCount = totalCount || data.length;

      ctx.results.innerHTML = '';
      this._renderList(ctx.results, data);

      if (this.state.hasMore) {
        ctx.statusBar.textContent = `å…±æ‰¾åˆ° ${this.state.totalCount} æ¡ç»“æœï¼Œæ»šåŠ¨æˆ–è€…ç‚¹å‡»åŠ è½½æ›´å¤š`;
      } else {
        ctx.statusBar.textContent = data.length > 0
          ? 'å·²åŠ è½½å®Œæ¯•'
          : 'æœªæ‰¾åˆ°åŒ¹é…çš„ç»“æœ';
      }

      ctx.loadMoreBtn.disabled = !this.state.hasMore;
    } catch (err) {
      ctx.results.innerHTML = `<div class="analects-error">æœç´¢å¤±è´¥ï¼š${err.message}</div>`;
      ctx.statusBar.textContent = 'æœç´¢å¤±è´¥';
    } finally {
      this.state.isLoading = false;
    }
  }

  async _loadMore(ctx) {
    if (!this.state.didSearch || this.state.isLoading || !this.state.hasMore) return;
    const token = this.state.searchToken;
    this.state.isLoading = true;
    ctx.loadMoreBtn.disabled = true;
    ctx.loadMoreBtn.textContent = 'åŠ è½½ä¸­...';

    try {
      const { data, hasMore } = await this.fetchEntries(this.state.currentFilters, this.state.page, this.state.pageSize);
      if (token !== this.state.searchToken) return;
      this.state.page += 1;
      this.state.hasMore = hasMore;
      this._renderList(ctx.results, data);

      if (this.state.hasMore) {
        ctx.statusBar.textContent = `å…±æ‰¾åˆ° ${this.state.totalCount} æ¡ç»“æœï¼Œæ»šåŠ¨æˆ–è€…ç‚¹å‡»åŠ è½½æ›´å¤š`;
        ctx.loadMoreBtn.disabled = false;
        ctx.loadMoreBtn.textContent = 'åŠ è½½æ›´å¤š';
      } else {
        ctx.statusBar.textContent = 'å·²åŠ è½½å®Œæ¯•';
        ctx.loadMoreBtn.textContent = 'å·²åŠ è½½å®Œæ¯•';
      }
    } catch (err) {
      // ä¿æŒä½“éªŒ
      ctx.statusBar.textContent = `åŠ è½½æ›´å¤šå¤±è´¥ï¼š${err.message}`;
      ctx.loadMoreBtn.disabled = false;
      ctx.loadMoreBtn.textContent = 'åŠ è½½æ›´å¤š';
    } finally {
      this.state.isLoading = false;
    }
  }

  _renderList(container, items) {
    const frag = document.createDocumentFragment();
    (items || []).forEach(e => {
      const card = document.createElement('div');
      card.className = 'analects-item';
      card.innerHTML = `
        <div class="meta">${this._fmtChapter(e)}</div>
        <div class="text">${this._safe(e.original_text)}</div>
        ${e.translation ? `<div class="subtext">${this._safe(e.translation)}</div>` : ''}
        ${e.annotation ? `<div class="subtext muted">${this._safe(e.annotation)}</div>` : ''}
      `;
      frag.appendChild(card);
    });
    container.appendChild(frag);
  }
}